<template>
	<view class="ticketrecord">
		   <view class="record-box" v-for="item in list">
		   	    <view class="title">
		   	    	{{item.msg}}
		   	    </view>
				<view class="time">
					{{item.ctime}}
				</view>
		   </view>
		   <view class="none" v-if="list==''">
		   	<image src="../../static/none.png" mode=""></image>
		   	<view>暂无数据</view>
		   </view> 
	</view>
</template>

<script>
	import {
		API
	} from '@/util/api.js';
	export default {
		data() {
			return {
				list:[],
				page:1,
				limit:10
			}
		},
		onLoad(option) {
		   this.request(API.getMyTickLog ,'get',{
			   page:this.page,
			   limit:this.limit
		   }).then(res => {
		   	console.log(res)
		   	if (res.code === 10000) {
		   		this.list=res.data
		   	}
		   });  
		   uni.startPullDownRefresh();
		},
		onShow() {
			
		},
		methods: {
		   getMore(){
			   this.page+=1;
			   this.request(API.getMyTickLog,'get',{
			   			   page:this.page,
			   			   limit:this.limit
			   }).then(res => {
			   	console.log(res)
			   	if (res.code === 10000) {
			   		if(res.data!=''){
			   			this.list=this.list.concat(res.data)
			   		}else{
						uni.showToast({
							title: '没有更多数据了',
							duration: 2000
						});
						return;
			   		}
			   		
			   	}
			   });	 
		   }
			
		},
		onReachBottom() {
				  //阻止重复加载
				    if(timer !== null){
				    clearTimeout(timer)
				}
			  var timer=setTimeout(()=>this.getMore(),500)
				             
		},
		onPullDownRefresh() {
			var timer=setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
			console.log(timer)
		}

	}
</script>

<style lang="scss">
	@import '../../assets/css/ticket/ticketrecord.scss';
</style>
